<template>
  <el-table :data="tableData" :border="true" style="width: 100%;">
    <el-table-column prop="text" label="名称" width="150"></el-table-column>
    <el-table-column prop="value" label="值">
      <template v-slot:default="scope">
        <el-input v-model="scope.row.value" />
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesDefaultValue",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: []
    };
  },
  created() {
    let rows = dsf.mix(true, {}, this.owner.metadata.defaultValue);
    let valueAttributes = dsf.mix(true, [], this.owner.metadata.valueAttributes);
    let data = this.currentData;
    // let r = dsf.mix(true, [], rows, data);
    let r=[];
    _.each(valueAttributes,(va)=>{
      let v={
        code:va.code,
        text:va.name,
        value:rows[va.code]
      }
      r.push(v);
    })
    this.tableData = r;
  },
  methods: {
    yes() {
      let result = {}
      _.each(this.tableData, (row) => {
        result[row.code] = row.value||"";
      });
      return result;
    }
  }
};
</script>
