<template>
  <div class="content">
    <div class="ds-tree-box fl-box">
      <SelectTree
        :tree-data="flowData"
        :select.sync="selectFlow"
        :checked="checked"
        :show-search="true"
      ></SelectTree>
    </div>
    <div class="fr-box">
      <div class="top">
        <div class="inline">
          <label class="form-label">表单名称：</label>
          <div class="input-inline" :title="curNode && curNode.pageTitle">
            {{ (curNode && curNode.pageTitle) || "暂无" }}
          </div>
        </div>
        <el-button class="ds-button small chooseForm" @click="chooseForm()">
          选择表单
        </el-button>
        <el-button class="ds-button small applyTo" @click="applyTo()">
          应用于
        </el-button>
      </div>
      <el-tabs v-model="selectTab" class="tabs">
        <el-tab-pane label="元素控制" name="0" class="pa">
          <SelectTable
            :data="(curNode && curNode.ctrls) || []"
            :columns="eleColumns"
          ></SelectTable>
        </el-tab-pane>
        <el-tab-pane label="操作控制" name="1">
          <SelectTable
            :data="(curNode && curNode.btns) || []"
            :columns="btnsColumns"
          ></SelectTable>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import SelectTree from "./selectTree";
import SelectTable from "./selectTable";

export default {
  name: "LinkSet",
  components: {
    SelectTree,
    SelectTable,
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      flowData: [],
      selectFlow: null,
      checked: [],
      sID: "",
      curNode: null,
      selectTab: 0,
      eleColumns: [
        {
          prop: "labelCaption",
          label: "控件名称",
        },
        {
          prop: "readOnly",
          label: "只读",
          width: 120,
          isSelect: true,
          readOnlyAll: false,
          isIndeterminate: false,
        },
        {
          prop: "hidden",
          label: "隐藏",
          width: 120,
          isSelect: true,
          hiddenAll: false,
          isIndeterminate: false,
        },
      ],
      btnsColumns: [
        {
          prop: "text",
          label: "按钮名称",
        },
        {
          prop: "readOnly",
          label: "可用",
          width: 120,
          isSelect: true,
          readOnlyAll: false,
          isIndeterminate: false,
        },
      ],
    };
  },
  watch: {
    selectFlow(val) {
      this.curNode = val;
    },
  },
  created() {
    this.sID = this.owner.chooseFlow && this.owner.chooseFlow.dsfa_rm_id;
    this.initFlow();
  },
  mounted() {},
  methods: {
    yes() {
      const data = dsf.mix(true, {}, this.flowData[0]);
      data.NodeList = data.children;
      delete data.children;
      return this.$http
        .post("wfk/saveExWF", { sData: JSON.stringify(data) }, $$webRoot.dsfa)
        .then((response) => {
          if (response.data.state == 20000) {
            return this.flowData;
          } else {
            dsf.layer.message(response.message || "环节设置异常", false);
          }
        });
    },
    validate() {
      return true;
    },
    // 获取form树
    initFlow() {
      if (this.currentData && this.currentData.length > 0) {
        const data = dsf.mix(true, [], this.currentData);
        data[0].children = data[0].NodeList;
        this.checked = [data[0].children[0].id];
        this.curNode = data[0].children[0];
        this.flowData = data;
        return;
      }
      this.$http
        .get("wfk/getWFNodeList", { sID: this.sID }, $$webRoot.dsfa)
        .then((response) => {
          if (response.data.success) {
            const data = this.formatData(response.data.data, undefined);
            if (data && data.children) {
              this.checked = [data.children[0].id];
              this.curNode = data.children[0];
            }
            this.flowData = [data];

            // if (this.currentData && this.currentData.dsfa_rm_id) {
            //   this.checked = [this.currentData.dsfa_rm_id];
            //   this.selectFlow = this.currentData;
            // }
          }
        });
    },
    initDefault() {
      let ctrls = [];
      let btns = [];
      dsf.recursionComponentProps(this.owner.$$getComponent().$page.$props, (data) => {
        if (data.isFormControl) {
          ctrls.push({
            id: data.id,
            label: data.label,
            labelCaption: `${data.label}(${data.caption})`,
            caption: data.caption,
            readOnly: false,
            hidden: false,
          });
        }
        if (data.ctrlName && data.ctrlName === "DsfButtonBar") {
          btns.push(...data.buttons);
        } 
      });
      return {
        ctrls: ctrls,
        btns: btns
      }
    },
    formatData(data, disabledVaule, isChild) {
      data.label = data.WFName || data.NodeName;
      data.id = data.WFID || data.NodeID;
      if (isChild) {
        const defaultData = this.initDefault();
        data.pageTitle = "";
        data.ctrls = defaultData.ctrls || [];
        data.btns = defaultData.btns || [];
        data.NameSpace = "";
      }
      if (disabledVaule === undefined) {
        data.disabled = data.NodeType === disabledVaule ? true : false;
      }
      data.children = data.NodeList;
      if (data.children) {
        for (var i = 0; i < data.children.length; i++) {
          this.formatData(data.children[i], disabledVaule, true);
        }
      }
      return data;
    },
    chooseForm() {
      let params = {
        type: "form",
      };
      dsf.layer.pc.openDialog({
        title: "选择表单",
        width: "400px",
        height: "400px",
        overflow: "hidden",
        content: "ChooseForm",
        params: params,
        btns: [
          {
            text: "确定",
            handler: (component) => {
              if (component.validate()) {
                const form = component.yes();
                let path = form.ID;
                path = path.replace("." + form.code, "");
                const url = dsf.url.getWebPath(
                  `/pc/views/${path}/${form.code}.layout.html`
                );
                this.$http
                  .get(url, null, { responseType: "text" })
                  .then((res) => {
                    if (res.data) {
                      let dom = $("<div>" + res.data + "</div>");
                      let layoutStr =
                        dom.children("script[id='layout']").html() || "";
                      let layoutFn = new Function(
                        [
                          "var layout=" + (layoutStr || "null") + ";",
                          "return layout;",
                        ].join("\n")
                      );
                      const layout = layoutFn();
                      this.curNode.pageTitle = layout.title;
                      this.curNode.ctrls = this.getFormctrls(layout); // 列表项
                      this.curNode.btns = this.getBtns(layout); // 按钮
                      this.curNode.NameSpace = form.ID;
                    }
                  });

                return true;
              }
              return false;
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    applyTo() {
      let params = { data: this.flowData };
      dsf.layer.pc.openDialog({
        title: "应用于",
        width: "400px",
        height: "400px",
        overflow: "hidden",
        content: "ApplyTo",
        params: params,
        btns: [
          {
            text: "确定",
            handler: (component) => {
              const nodes = component.yes();
              nodes.forEach((node) => {
                if (node.NodeID) {
                  this.flowData.forEach((data) => {
                    data.children.forEach((item) => {
                      if (node.id === item.id) {
                        item.pageTitle = this.curNode.pageTitle;
                        item.ctrls = dsf.mix(true, [], this.curNode.ctrls);
                        item.btns = dsf.mix(true, [], this.curNode.btns);
                        item.NameSpace = this.curNode.NameSpace;
                      }
                    });
                  });
                }
              });
              return true;
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    getFormctrls(data) {
      let arr = [];
      if (data.isFormControl) {
        arr.push({
          id: data.id,
          label: data.label,
          labelCaption: `${data.label}(${data.caption})`,
          caption: data.caption,
          readOnly: false,
          hidden: false,
        });
      } else {
        data.slots.forEach((slot) => {
          slot.controls.forEach((control) => {
            const item = this.getFormctrls(control);
            arr.push(item);
          });
        });
      }
      return arr.flat(Infinity);
    },
    getBtns(data) {
      let arr = [];
      if (data.ctrlName && data.ctrlName === "DsfButtonBar") {
        arr.push(...data.buttons);
      } else {
        data.slots.forEach((slot) => {
          slot.controls.forEach((control) => {
            const item = this.getBtns(control);
            arr.push(item);
          });
        });
      }
      return arr.flat(Infinity);
    },
  },
};
</script>
<style lang="scss" scoped>
