  position: relative;
  height: 100%;
}
.ds-tree-box {
  float: left;
  width: 35%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
}
.fr-box {
  position: relative;
  width: 65%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
  border-left: 1px solid #e6e6e6;
  float: left;
  height: 100%;
  box-sizing: border-box;
  .tabs {
    padding: 0 20px;
  }
}
.dsf-tree-search {
  /deep/ .el-input__suffix {
    cursor: pointer;
  }
}
.top {
  .inline {
    margin: 0;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    .form-label {
      float: left;
      display: block;
      font-weight: 400;
      line-height: 20px;
      text-align: right;
      padding: 9px 9px;
      width: 92px;
    }
    .input-inline {
      display: inline-block;
      float: none;
      line-height: 38px;
      white-space: nowrap;
      width: 176px;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
</style>
