<template>
  <el-container style="height:100%">
    <el-aside width="25%" style="overflow:auto;height:100%">
      <div style="overflow:auto;height:100%">
        <el-tree :data="treeData" :props="defaultProps" default-expand-all>
          <span slot-scope="{node,data}" class="custom-tree-node" @dblclick="toList(node,data)">
            <i class="iconfont icon-biaodanzujian-shurukuang" style="margin-right:5px;"></i>
            <span>{{ node.label }}</span>
            <span v-if="data.field" style="color:red">(字段:{{ data.field }})</span>
          </span>
        </el-tree>
      </div>
    </el-aside>
    <el-main style="padding:0px">
      <el-container>
        <el-main>
          <el-table :data="tableData" stripe :highlight-current-row="true" :row-key="getRowId" :tree-props="{children: 'children'}">
            <el-table-column width="30" align="center"></el-table-column>
            <el-table-column label="名称" width="150">
              <template v-slot:default="scope">
                <el-input v-if="scope.row.type=='metadata'" v-model="scope.row.fieldCaption"></el-input>
                <span v-else-if="scope.row.type=='valueAttributes'">{{ scope.row.fieldCaption }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="code" label="代码"></el-table-column>
            <el-table-column label="类型">
              <template v-slot:default="scope">
                <el-select v-if="'metadata'===scope.row.type" v-model="scope.row.searchType" @change="searchTypeChange($event,scope.row)">
                  <el-option v-for="op in options" :key="op.value" :value="op.value" :label="op.label"></el-option>
                </el-select>
                <el-select v-if="'valueAttributes'===scope.row.type" v-model="scope.row.join">
                  <el-option :value="1" label="参与检索"></el-option>
                  <el-option :value="0" label="不参与检索"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="默认值(选中)">
              <template v-slot:default="scope">
                <el-input v-if="'metadata'===scope.row.type" v-model="scope.row.defaultValue"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="布局">
              <template v-slot:default="scope">
                <el-select v-if="scope.row.type==='metadata'" v-model="scope.row.layout">
                  <el-option :value="'24'" label="整行"></el-option>
                  <el-option :value="'12'" label="1/2"></el-option>
                  <el-option :value="'8'" label="1/3"></el-option>
                  <el-option :value="'6'" label="1/4"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="显示方式">
              <template v-slot:default="scope">
                <el-select v-if="scope.row.type==='metadata'&&scope.row.searchType=='enum'" v-model="scope.row.model">
                  <el-option :value="'select'" label="下拉框"></el-option>
                  <el-option :value="'tile'" label="平铺"></el-option>
                </el-select>
                <el-select v-if="scope.row.type==='metadata'&&scope.row.searchType=='text'" v-model="scope.row.model">
                  <el-option :value="'textbox'" label="文本框"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="操作符">
              <template v-slot:default="scope">
                <el-select v-if="scope.row.type==='valueAttributes'||(scope.row.type==='metadata'&&!scope.row.children)" v-model="scope.row.operator">
                  <el-option label="等于" value="eq"></el-option>
                  <el-option label="包含" value="in"></el-option>
                  <el-option label="大于" value="gt"></el-option>
                  <el-option label="小于" value="lt"></el-option>
                  <el-option label="大于等于" value="gteq"></el-option>
                  <el-option label="小于等于" value="lteq"></el-option>
                  <el-option label="区间" value="between"></el-option>
                  <el-option label="匹配" value="like"></el-option>
                  <el-option label="匹配开始" value="slike"></el-option>
                  <el-option label="匹配结束" value="elike"></el-option>
                  <!-- <el-option label="区间()" value="range"></el-option> -->
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="130px" align="center" header-align="center">
              <template v-slot:default="scope">
                <template v-if="scope.row.type==='metadata'">
                  <dsf-button v-if="scope.row.searchType=='enum'" title="选项配置" size="small" btn-style="icon-text" icon="shezhi" @click.stop="settingEnumItems(scope.row)"></dsf-button>
                  <dsf-button title="删除" size="small" btn-style="icon-text" icon="guanbi2" @click.stop="remove(scope.row)"></dsf-button>
                  <dsf-button title="上移" size="small" btn-style="icon-text" icon="shang" @click.stop="up(scope.row)"></dsf-button>
                  <dsf-button title="下移" size="small" btn-style="icon-text" icon="xia" @click.stop="down(scope.row)"></dsf-button>
                </template>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from '_platform/dsf/mixins/propertyDialogSetting';
export default {
  name: 'DesSuperSearchSetting',
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      treeData: [],
      defaultProps: {
        "children": "children",
        "label": "name"
      },
      options: [
        { 'label': '选项枚举', 'value': 'enum', 'defaultField': ['value'] },
        { 'label': '文本', 'value': 'text' },
        { 'label': '日期时间', 'value': 'datetime' }
      ]
    }
  },
  created() {
  },
  async mounted() {
    let grid = this.owner.$$getComponent();
    let metadata = grid.$options.design.metadata.create.call(grid);
    if (!metadata || metadata.length <= 1) {
      dsf.layer.pc.message("请将要展示的元数据拖动到列表中", false);
      return;
    }
    let metadatakey = metadata[1].metadata.at;
    if (!metadatakey) {
      dsf.layer.pc.message("请将要展示的元数据拖动到列表中", false);
      return;
    }
    this.treeData = await this.getTreeData(metadatakey);
    this.tableData = this.currentData || [];
  },
  methods: {
    getRowId(row) {
      let key = row.id;
      return key
    },
    getParentRow(row) {
      let r = _.find(this.tableData, (r) => r.id == row.parent);
      return r || {};
    },
    searchTypeChange(v, row) {
      this.updateValueAttributeJoin(row, v);
    },
    updateValueAttributeJoin(row, v) {
      if (row.searchType == 'enum') {
        row.model = 'select';
      }
      else if (row.searchType == 'text') {
        row.model = 'textbox';
      }
      // else {
      //   row.model = 'textbox';
      // }
      let op = _.find(this.options, (o) => o.value === v);
      if (op && row?.children?.length > 1) {
        let dfs = op.defaultField || [];
        _.each(row.children, (c) => {
          if (dfs.length > 0) {
            let result = _.filter(dfs, (f) => f === c.code);
            c.join = ~~(result.length > 0);
          }
          else {
            c.join = 1;
          }
        });
      }
    },
    toList(node, data) {
      if (data.field) {
        let index = _.findIndex(this.tableData, (row) => {
          return row.field == data.field;
        })
        if (index < 0) {
          var row = {
            'id': data.id,
            'field': data.field,
            'fieldCaption': data.fieldCaption,
            'type': "metadata",
            "searchType": "enum",
            'code': data.code,
            'layout': '24',
            'model': 'select',
            'defaultValue': "",
            'operator': 'eq'
            // 'items':[]
          }
          if (data.valueAttributes.length > 1) {
            row.children = [];
            let children = _.map(data.valueAttributes, (d) => {
              return {
                id: data.id + "_" + d.code,
                field: data.field + "_" + d.code,
                code: d.code,
                fieldCaption: data.fieldCaption + "(" + d.name + ")",
                type: 'valueAttributes',
                join: 1,
                parent: data.id,
                defaultValue: "",
                operator: 'eq'
              }
            });
            row.children.push(...children)
          }
          this.updateValueAttributeJoin(row, row.searchType);
          this.tableData.push(row);
        }
      }
    },
    settingEnumItems(row) {
      this.$openDialog({
        title: "选项设置",
        content: 'DesSuperSearchEnumItems',
        height: "60vh",
        width: "700px",
        params: {
          "item": row,
          "searchItems": this.tableData,
          "currentData": row.setting
        },
        btns: [
          {
            text: '确定',
            handler(res) {
              let result = res.yes();
              row.setting = result || null;
            }
          },
          {
            text: "取消"
          }
        ]
      })
    },
    async getTreeData(metadatakey) {
      let params = {
        namespace: metadatakey
      };
      let data = await this.$webAPI.getMetaDataTree(params, false);
      return data;
      // this.$webAPI.getMetaDataTree(params, false)
      //   .then(data => {
      //     this.treeData = data;
      //   });
    },
    remove(row) {
      dsf.array.remove(this.tableData, row);
    },
    up(row) {
      let index = _.findIndex(this.tableData, (r) => r == row);
      if (index > 0) {
        let targetRow = this.tableData[index - 1];
        this.$set(this.tableData, index - 1, row);
        this.$set(this.tableData, index, targetRow);
      }
    },
    down(row) {
      let index = _.findIndex(this.tableData, (r) => r == row);
      if (index < this.tableData.length - 1) {
        let targetRow = this.tableData[index + 1];
        this.$set(this.tableData, index + 1, row);
        this.$set(this.tableData, index, targetRow);
      }
    },
    yes() {
      let result = _.map(this.tableData, (r) => {
        let row = dsf.mix(true, {}, r);
        if (row.searchType != "enum") {
          delete row.items;
        }
        return row;
      })
      return result
    }
  }
}
</script>
<style scoped>
/* .el-table__expand-icon + .fieldname,.el-table__placeholder+ .fieldname{
  float: left;
  margin-left: 30px;
  position:absolute;
  top:12px;
  height:40px;
  width:auto;
} */
</style>