<template>
  <MonacoEditor ref="editor" :language="language" @mounted="editorLoaded"></MonacoEditor>
</template>
<script>
export default {
  name: "EventSetting",
  props: {
    width: {
      type: [String, Number],
      default: "100%",
    },
    height: {
      type: [String, Number],
      default: "100%",
    },
    code: {
      type: String,
      default: "",
    },
    srcPath: {
      type: String,
      default: "",
    },
    language: {
      type: String,
      default: "javascript",
    },
    theme: {
      type: String,
      default: "vs-dark",
    }, // vs, hc-black
    options: {
      type: Object,
      default: () => {},
    },
    changeThrottle: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {};
  },
  created() {},
  mounted() {},
  methods: {
    getContent() {
      if (this.language == "html") {
        return dsf.escapeFilter(this.$refs.editor.getContent());
      } else {
        return this.$refs.editor.getContent();
      }
    },
    editorLoaded() {
      let v = this.code;
      if (this.language == "html") {
        v = dsf.unescapeHTML(v);
      }
      this.$refs.editor.setContent(v);
    },
  },
};
</script>